.Header{
    overflow: none;    
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    top: 10px;
    width: 100%;
}

.navbar {
    background-color: #1b1b1b; 
    padding: 5px 20px;
    border-radius: 50px; 
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    width:max-content;
    max-width: 100%;
    border: 1px whitesmoke solid
  }

  .header-fit{
    display: flex;
    flex-direction: row;
  }

  .circular{
    background-color: #00ff88;
    width:40px;
    aspect-ratio: 1/1;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
  }

  #arrow{
    width:65%;
    height: 65%;
    transform: rotateZ(90deg);
  }

  @media screen and (min-width: 1080px){
    .header-fit  .circular {
        display: none;
      }
  }
  

  .nav-list {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
  }
  
  .nav-item {
    margin: 0 15px;
    color: #ffffff;
    font-size: 18px;
    cursor: pointer;
    transition: color 0.3s ease, background-color 0.3s ease;
    padding: 8px 15px;
    border-radius: 20px;
    white-space: nowrap;
  }
  
  .nav-item.active {
    background-color: #00ff88; 
    color: #000000; 
  }
  
  .nav-item:hover {
    color: #00ff88; 
  }
  
  .nav-item.active:hover {
    color: #000000; 
  }

  .brand {
    font-size: 28px;
    font-weight: bold;
    color:#fff;
    margin: 0 40px;
    cursor: default;
  }

  .nav-item a{
    color: inherit;
    text-decoration: none;
  }


  /* Header Ended*/